___CSS_LOADER_EXPORT___.push([module.id, ".clickable[data-v-be74d778]{cursor:pointer}", "",{"version":3,"sources":["webpack://./resources/js/components/CreateActivity/Container.vue"],"names":[],"mappings":"AACA,4BACA,cACA","sourcesContent":["<style scoped>\n.clickable {\n    cursor: pointer\n}\n</style>\n<style src=\"vue-multiselect/dist/vue-multiselect.min.css\"></style>\n\n<template>\n    <div>  \n        <!-- Referral Details Section -->\n        <div class=\"card mt-3\">\n            <div class=\"card-header\">\n                <h5 class=\"m-0\">\n                    Create New Activity\n                </h5>\n            </div>\n\n            <div class=\"card-body\">\n                <div class=\"container\">\n                    <div class=\"row\">\n                        <div class=\"col\">\n                            Date / Time\n                        </div>\n                        <div class=\"col-6\">\n                            <div class=\"form-group\">\n                                <label for=\"activity_date\" class=\"form-label\">Date</label>\n                                <input id=\"activity_date\" type=\"text\" name=\"activity_date\" class=\"form-control\" autocomplete=\"off\" placeholder=\"YYYY-MM-DD\">\n                                <!-- <HasError :form=\"referralForm\" field=\"activity_date\" /> -->\n                            </div>\n                            <div class=\"form-group\">\n                                <label for=\"activity_time\" class=\"form-label\">Time</label>\n                                <input id=\"activity_time\" type=\"text\" name=\"activity_time\" class=\"form-control\" autocomplete=\"off\" placeholder=\"22:00\">\n                                <!-- <HasError :form=\"referralForm\" field=\"activity_time\" /> -->\n                            </div>\n                            <div class=\"form-group\">\n                                <label for=\"location\" class=\"form-label\">Location</label>\n                                <select name=\"location\" id=\"location\" class=\"form-control\">\n                                    <option value='0' disabled>Choose</option>\n                                    <option value='0'>client home</option>\n                                    <option value='0'>Your office</option>\n                                    <option value='0'>Community center</option>\n                                </select>\n                                <!-- <HasError :form=\"referralForm\" field=\"location\" /> -->\n                            </div>\n                        </div>\n                        <div class=\"col\">\n                            3 of 3\n                        </div>\n                    </div>\n                    <hr>\n                    <div class=\"row\">\n                        <div class=\"col\">\n                            Services\n                        </div>\n                        <div class=\"col-6\">\n                            <ul>\n                                <li v-for=\"recordBeneficiary in record.record_beneficiaries\" :key=\"recordBeneficiary.id\">\n                                    <span>{{ recordBeneficiary.individual.name}}</span>\n                                    <div class=\"form-group\" v-if=\"servicetypes\" >\n                                        <multiselect :id=\"recordBeneficiary.id\"\n                                        v-model=\"recordBeneficiaryForm.servicetypes\"\n                                        :options=\"servicetypes\"\n                                        :multiple=\"true\"\n                                        :close-on-select=\"false\"\n                                        :clear-on-select=\"false\" \n                                        :preserve-search=\"true\"\n                                        placeholder=\"Pick some\"\n                                        label=\"name\" \n                                        track-by=\"name\" \n                                        :preselect-first=\"true\">\n                                            <!-- <template slot=\"selection\" slot-scope=\"{ values, search, isOpen }\"><span class=\"multiselect__single\" v-if=\"values.length &amp;&amp; !isOpen\">{{ values.length }} options selected</span></template> -->\n                                        </multiselect>\n                                        <!-- <pre class=\"language-json\"><code>{{ value  }}</code></pre> -->\n                                    </div>\n                                </li>\n                            </ul>\n                        </div>\n                        <div class=\"col\">\n                            3 of 3\n                        </div>\n                    </div>\n                    <hr>\n                </div>\n            </div>\n        </div>\n\n\n    </div>\n</template>\n\n<script>\nimport Form from 'vform'\nimport Multiselect from 'vue-multiselect'\nimport axiosMixin from '../../mixins/axiosMixin'\t\n\nexport default {\n\tmixins: [axiosMixin],\n    name: 'CreateActivity',\n    components: {\n        Multiselect,\n    },\n    props: {\n        recordId: {\n            type: String,\n            required: true\n        }\n    },\n    data() {\n        return{\n            record: {},\n            servicetypes: [],\n            recordBeneficiaryForm: new Form({\n\t\t\t\tid: '',\n\t\t\t\tservicetypes: [],\n\t\t\t})\n\n        }\n    },\n    methods: {\n        getRecord() {\n\t\t\tthis.$Progress.start();\n\t\t\taxios.get('/api/records/' + this.recordId)\n\t\t\t.then((data) => {\n\t\t\t\t// success\n                this.record = data.data.data;\n\t\t\t\tthis.$Progress.finish();\n\t\t\t})\n\t\t\t.catch((e) => {\n\t\t\t\t// error\n\t\t\t\tthis.$Progress.fail();\n                console.log(e);\n\t\t\t})\n\t\t},\n        getServicetypes() {\n\t\t\tthis.$Progress.start();\n\t\t\taxios.get('/api/servicetypes/')\n\t\t\t.then((data) => {\n\t\t\t\t// success\n                this.servicetypes = data.data.data;\n\t\t\t\tthis.$Progress.finish();\n\t\t\t})\n\t\t\t.catch((e) => {\n\t\t\t\t// error\n\t\t\t\tthis.$Progress.fail();\n                console.log(e);\n\t\t\t})\n\t\t},\n\n    },\n\n    created (){\n        this.getRecord();\n        this.getServicetypes();\n    },\n\n}\n</script>"],"sourceRoot":""}]);
